.hero-compare {
  margin-bottom: 76px;
  &__inner {
    overflow: hidden;
    border-radius: 20px;
    background: #f8f8f8;
    padding: 30px 40px;
    position: relative;
  }

  &__image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
    max-width: 380px;

    @media (max-width: 1140px) {
      display: none;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    margin-bottom: 14px;
    margin-top: 40px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 136%;
    letter-spacing: -0.02em;
    color: var(--gray);
    margin: 0;
    margin-bottom: 28px;
  }

  &__tags {
    display: flex;
    gap: 15px;

    .tag {
      border-radius: 100px;
      padding: 10px 22px;
      background: var(--white);
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--tekst);

      &.active {
        background: var(--accent);
        color: var(--white);
      }
    }
  }
}

.compare {
  margin-bottom: 100px;
  &__header {
    border-bottom: 1px solid #d8d8d8;
    margin-bottom: 40px;
    padding-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    position: relative;
    @media (max-width: 992px) {
      margin-bottom: 60px;
    }
  }

  &__tab {
    display: none;

    &.isActive {
      display: block;
    }
  }

  .compare-slider-nav {
    display: flex;
    align-items: center;
    gap: 10px;
    @media (max-width: 640px) {
      position: absolute;
      bottom: -40px;
      right: 0;
    }
    .compare-slider-nav-btn {
      cursor: pointer;

      svg {
        width: 28px;
        height: 28px;
        fill: var(--accent);
        transition: var(--transition);
      }

      @media (any-hover: hover) {
        &:hover {
          svg {
            fill: var(--hovered);
          }
        }
      }
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    gap: 56px;
    @media (max-width: 992px) {
      gap: 20px;
    }
    &-btn {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: clamp(14px, 2vw, 18px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #b7b7b7;
      max-width: fit-content;
      position: relative;
      transition: var(--transition);
      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
        }
      }
      &:after {
        content: "";
        width: 100%;
        height: 1px;
        position: absolute;
        left: 0;
        bottom: -13px;
        z-index: 1;
        background: var(--accent);
        display: none;
      }

      &.active {
        color: var(--accent);
        &:after {
          display: block;
        }
      }
    }
  }

  &__slide {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: auto;
    border-radius: 10px;
    &-about {
      background: var(--blue);
      border-radius: 10px;
      position: relative;
      display: flex;
      flex-direction: column;
      margin-bottom: 21px;
      padding: 31px 15px 22px 15px;
      .btn-del {
        position: absolute;
        top: 15px;
        right: 15px;
        cursor: pointer;
        background: var(--accent);
        border-radius: 5px;
        width: 20px;
        height: 20px;
        display: grid;
        place-items: center;
        svg path {
          stroke: var(--white);
        }
        @media (any-hover: hover) {
          &:hover {
            background: var(--hovered);
          }
        }
      }

      .logo {
        display: flex;
        justify-content: center;
        margin-bottom: 22px;
      }
      .title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: clamp(16px, 2vw, 20px);
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        margin-bottom: 13px;
      }
      .desc {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
    }
  }

  .list {
    display: flex;
    flex-direction: column;
    margin-bottom: 29px;
    li {
      display: flex;
      flex-direction: column;
      gap: 13px;
      padding-block: 12px;
      &:nth-child(3) {
        background: var(--blue);
      }
      span {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        opacity: 0.5;
        padding-inline: 14px;
      }
      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        padding-inline: 14px;
      }
    }
  }

  .actions {
    display: flex;
    flex-direction: column;
    gap: 10px;

    &__btn {
      background: var(--accent);
      color: var(--white);
      padding-block: 10px;
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }
      &.disable {
        opacity: 0.5;
      }
    }
  }
}
